.child
{
  width: 20%;
  border: 1px solid #ddd;;
  padding: 1%;
  margin: 1%;
  box-shadow: 0px -8px 8px -8px #000;
  text-align: center;
  background-color: red;
  color: bisque;
}

.parent
{
  display: flex;
  justify-content: center;
  background-color: bisque;
}

.child img{
  width: 100%;
  border-radius: 50%;
}

@media (min-width:300px) and (max-width:600px)
{
  .child
  {
  width: 20%;
  padding: 6%;
  background-color: chocolate;
  color: yellow;
  }
  .parent
  {
    width: 100%;
    display: flow-root;
  }
  .child img{
    width: 20%;
    border-radius: 50%;
  }
  .child1
  {
    width: 10%;
  }
}

.link
{
  text-decoration: none;
}

.btn
{
  
  background-color: beige;
  color: blue;
  padding: 1%;
  //border-radius: 50%;
  display: block;
  text-align:center;
}

.child a
{
  color: green;
}
.child1
{
  width: 30%;
  text-align:center;
  background-color: brown;
  display: block;
  margin: 1%;
  padding: 1%;
  border-radius: 50%;
  color: rgb(0, 144, 200);
}
.child2
{
  width: 40%;
  background-color: green;
  color: aliceblue;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
  padding: 1%;
  margin: 1%;
  display: block;
  border-radius: 30%;
}



.back
{
  text-align: center;
  background-color: grey;
}
.skills
{
  margin: 2%;
  padding: 1%;
  color: blue;
  background-color: bisque;
  border-radius: 6px;
}